import React from 'react';
import what from '../../assets/img/2fa3.svg';
import why from '../../assets/img/2fa2.svg';
import who from '../../assets/img/who.png';
const About = () =>{
    return(
        <section className="container p-2 mt-2 mb-2" id='about'>
            <h1 className="display-1">About FaceIT</h1>
            <span className="text-warn">What ~ Why ~ Who</span>
            <div id="what">
                <h2 className='display-2 text-success'>What</h2>
                <div className="container-fluid row p-2">
                    <div className="col-sm-12 col-md-6 col-lg-6 p-3">
                        <img src={what} alt="What is FaceIT" className='img-fluid' />
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6">
                        <article aria-label='What is FaceIT?'>
                            While there is an abundance of file lockers for our hand-held mobile devices
                            regardless of the operating system, there is a scarcity for such file lockers when it
                            comes to our day-to-day computers and laptops, be it on Linux, Mac OS or for the
                            majority of us, Windows.
                            <br/><br/>
                            While there are software systems that can achieve the said task, majority of the aforesaid software systems only “lock” the files that the user chooses by putting it away in a hidden system location and locking the location with a user-defined passphrase. While technically this accomplishes what the software says it would, it does not necessarily “encrypt” the files themselves.
                            <br/><br/>
                            <strong>FaceIT</strong> aims to solve this loophole whilst proving the users with a mobile/web/software interface package so they can have total control over their files at all times, regardless of their OS platform.
                        </article>
                    </div>
                </div>
            </div>
            <div id="why">
                <h2 className='display-2 text-success'>Why</h2>
                <div className="container-fluid row p-2">
                    <div className="col-sm-12 col-md-6 col-lg-6 p-3">
                        <img src={why} alt="What is FaceIT" className='img-fluid' />
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6">
                        <article aria-label='Why use FaceIT?'>
                            <strong>FaceIT</strong> aims at significantly cutting down on the drawbacks of existing systems while also ensuring that the data the software is entrusted with is kept safe and untampered with.
                            <br/><br/>
                            The primary concern of using an online encrypted storage system is that there is
                            little to no guarantee that the data that is being encrypted is not shared/sold for
                            advertising or quality assurance purposes.
                            <br/><br/>
                            Hence, <strong>FaceIT</strong> stores all your data locally with a checksum for manual verification.
                        </article>
                    </div>
                </div>
            </div>
            <div id="who">
                <h2 className='display-2 text-success'>Who</h2>
                <div className="continaer-fluid p-2 row">
                    <div className="col-sm-12 col-md-6 col-lg-6 p-3">
                        <img src={who} className="hvr-forward" height="450" alt="Abhishek Iyengar - Founder of FaceIT"  />
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6">
                        <div className="container p-2">
                            <h2 className="display-3 text-info">Abhishek Iyengar</h2>
                            <h4 className="text-muted">Founder and Developer</h4>
                            <article aria-label="Founder's notes">
                                Hi! I am a final year MSc. Computer Science student of Patkar Varde College, Goregaon and am the solo developer of this face-recognition based encryption system I chose to call <strong>FaceIT</strong>.
                                <br/>
                                <br/>
                                Apart from being one-of-a-kind project, the other reason I chose to this concept is to build up a stronger base at being a full-stack developer and having this project under my belt makes impressing the recruiters all that much easier. 
                            </article>
                            <br/>
                            <hr/>
                            <br/>
                            <div className="d-flex justify-content-around" id="socials">
                                <a href="https://www.linkedin.com/in/abhishek-i-4ba8968a/" rel="noreferrer noopener" target="_blank" className="nav-link" title="Connect with me on LinkedIN"><i className="fab fa-linkedin fa-2x"></i></a>
                                <a href="https://github.com/tinshade" rel="noreferrer noopener" target="_blank" className="nav-link" title="Follow me on Github"><i className="fab fa-github fa-2x"></i></a>
                                <a href="https://twitter.com/tinshade" rel="noreferrer noopener" target="_blank" className="nav-link" title="Follow me on Twitter"><i className="fab fa-twitter fa-2x"></i></a>
                                <a href="https://youtube.com/AnnoyingErrors41/" rel="noreferrer noopener" target="_blank" className="nav-link" title="Subscribe to me on Youtube"><i className="fab fa-youtube fa-2x"></i></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default About;