import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch, Redirect } from "react-router-dom";

import AdminLayout from "layouts/Admin/Admin.js";
import CommonLayout from "layouts/Common/Common.js";
import Home from 'views/Home';
import "assets/scss/black-dashboard-react.scss";
import "assets/demo/demo.css";
import "assets/css/custom.css";
import "assets/css/hover-min.css";
import "assets/css/nucleo-icons.css";
import { Provider } from "react-redux";
import store from './store';
const hist = createBrowserHistory();

ReactDOM.render(
  <Provider store={store}>
    <Router history={hist}>
      <Switch>
        <Route path="/admin" render={props => <AdminLayout {...props} />} />
        {/* <Route path="/common" render={props => <CommonLayout {...props} />} /> */}
        <Route path="/common/home" render={props => <CommonLayout {...props} />} />
        <Route path="/common/register" render={props => <CommonLayout {...props} />} />
        {/* <Redirect from="/" to="/admin/dashboard" /> */}
        <Redirect from="/" to="/common/home" />
      </Switch>
    </Router>
  </Provider>
  ,
  document.getElementById("root")
);
