import React, {useState, useEffect} from 'react';
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Row,
    Col
  } from "reactstrap";
import axios from 'axios';
import { connect } from 'react-redux';

const Pseudousers = ({refresh}) =>{
  const [pseudo, setPseudo] = useState([]);
  const [addUser, setAddUser] = useState(false);
  const [deleteUser, setDeleteUser] = useState(false);
  const getPseudo = async () =>{
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': String(refresh)
      }
    };
    try {
      const res =  await axios.get(`http://127.0.0.1:8000/pseudo/`, config);
      setPseudo(res.data.pseudoers)
    } catch (err) {
      console.log(err);
    }
  }

  const handleSubmit = (e) =>{
    e.preventDefault();
    addPseudo(username,password,first_name,last_name);
  }
  const [formData, setFormData] = useState({
    username : '',
    password : '',
    first_name : '',
    last_name : ''
  });
  const { username, first_name, last_name, password, re_password } = formData;

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  //! Adding pseudousers
  const addPseudo = async (username,password,first_name,last_name, user=16) => {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': String(refresh)
      }
    };
    const body = JSON.stringify({ username, password, first_name, last_name,user});
    console.log(body)
    try {
      await axios.post(`http://localhost:8000/pseudo/`, body, config);
      setAddUser(true);
    } catch (err) {
      console.log(err);
    }    
  };

  //! Updaing pseudo users
  const editPseudo = async (id) =>{
    console.log(id);
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': String(refresh)
      }
    };
    const body = JSON.stringify({ username, first_name, last_name, password, re_password, id });
    try {
      const res =  await axios.put(`http://127.0.0.1:8000/pseudo/`, body, config);
    } catch (err) {
      console.log(err);
    } 
  }

  //! Deleting pseudousers
  const deletePseudo = async (id) =>{
    var data = JSON.stringify({"id":parseInt(id)});

    var config = {
      method: 'delete',
      url: 'http://localhost:8000/pseudo/',
      headers: { 
        'Content-Type': 'application/json', 
      },
      data : data
    };

    axios(config)
    .then(function (response) {
      if(response.status === 204){
        setDeleteUser(true);
      }
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  useEffect(()=>{
    getPseudo();
    setAddUser(false);
    setDeleteUser(false);
    return;
  },[addUser, deleteUser]);
  return(
      <>
        <div className="content content-fit">
          <Row>
            <Col md="8">
              <Card>
                <CardHeader>
                  <h5 className="title">Add PseudoUsers</h5>
                </CardHeader>
                <CardBody>
                  <form onSubmit={e=>handleSubmit(e)}>
                    <Row>
                      <Col md="12">
                        <FormGroup>
                          <label>Username</label>
                          <input
                            autoFocus
                            placeholder="Type in a cool username!"
                            className="form-control"
                            type="text"
                            name="username"
                            value={username}
                            minLength="4"
                            maxLength="30"
                            required
                            style={{color: "white !important" }}
                            onChange = {e => onChange(e)}
                          />
                          <div className="container">
                            <small className="pr-2 text-info">Keep it between 4-30 characters.</small>
                            <br/>
                            <small className="pr-2 text-warning">Special characters are not supported!</small>
                          </div>
                        </FormGroup>
                      </Col>
                    </Row>

                    <Row>
                      <Col md="6">
                        <FormGroup>
                          <label>First Name</label>
                          <input
                            placeholder="What's their first name?"
                            className="form-control"
                            type="text"
                            name="first_name"
                            value={first_name}
                            maxLength="30"
                            required
                            style={{color: "white !important" }}
                            onChange = {e => onChange(e)}
                          />
                        </FormGroup>
                      </Col>
                      <Col md="6">
                        <FormGroup>
                          <label>Last Name</label>
                          <input
                            placeholder="And their last name?"
                            className="form-control"
                            type="text"
                            name="last_name"
                            value={last_name}
                            maxLength="30"
                            required
                            style={{color: "white !important" }}
                            onChange = {e => onChange(e)}
                          />
                        </FormGroup>
                      </Col>
                    </Row>

                    <Row>
                      <Col md="6">
                        <FormGroup>
                          <label>Password</label>
                          <input
                            placeholder="Set a secret passphrase!"
                            className="form-control"
                            type="password"
                            name="password"
                            value={password}
                            minLength='6'
                            maxLength='100'
                            required
                            style={{color: "white !important" }}
                            onChange = {e => onChange(e)}
                          />
                        </FormGroup>
                      </Col>
                      <Col md="6">
                        <FormGroup>
                          <label>Confirm Password</label>
                          <input
                            placeholder="Repeat the secret passphrase!"
                            className="form-control"
                            type="password"
                            name="re_password"
                            value={re_password}
                            minLength='6'
                            maxLength='100'
                            required
                            style={{color: "white !important" }}
                            onChange = {e => onChange(e)}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Button className="btn-fill" color="primary" type="submit">
                      Add User
                    </Button>
                  </form>
                </CardBody>

              </Card>
            </Col>

            {/* DISPLAY PSEUDOUSERS SECTION */}
            <Col md="4">
              <Card >
                {
                  pseudo.length > 0 ?
                  pseudo.map((user,id) => (
                    <CardBody key = {id}>
                      <div className="border p-2 text-center container" style={{borderRadius: "10px"}}>
                        <h5 style={{textTransform: "uppercase"}}>{user.username}</h5>
                        <span style={{textTransform:"capitalize"}}>{user.first_name}&nbsp;{user.last_name}</span>
                        <br/>
                          {/* <i id={user.id} disabled className="fas fa-pen rounded-circle border border-primary mx-2 p-2" /> */}
                          <button 
                            onClick={()=>{deletePseudo(user.id)}} 
                            className="btn btn-danger"
                          > 
                            Delete
                          </button>
                      </div>
                    </CardBody>
                  )):(
                    <CardBody>
                      <div className="container px-3 py-5 mt-2 text-center">
                        <h4>You have no <strong className="text-primary">Pseudousers</strong></h4>
                      </div>
                    </CardBody>
                  )
                }
              </Card>
            </Col>
          </Row>
        </div>
      </>
  );
}
const mapStateToProps = state => ({
  refresh: state.auth.refresh
});

export default connect(mapStateToProps, null)(Pseudousers);